import { Component, OnInit } from '@angular/core';

import { ActivatedRouteSnapshot, ActivatedRoute, Router } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { UtilService } from 'src/app/services/util.service';
import { DialogService } from 'src/app/api/util/dialog.service';
import { ModalController, NavController } from '@ionic/angular';
import { AddResultPage } from '../add-result/add-result.page';
import * as moment from 'moment';
import { ListsHomeService } from 'src/app/services/lists-home.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { SharerService } from 'src/app/services/sharer.service';

 



@Component({
  selector: 'app-game',
  templateUrl: './game.page.html',
  styleUrls: ['./game.page.scss','../../home/games/games.page.scss'],
})
export class GamePage implements OnInit {

  public id : any;
  public gameModel : Model;
  public game : any ;
  private firstLoading = true;


  constructor(
    public route : ActivatedRoute,
    public requesService : RequestService,
    public util : UtilService,
    public dialogService : DialogService, 
    public modalController : ModalController,
    private router : Router,
    public navCtrl : NavController,
    public listsHomeService : ListsHomeService,
    public authUser : AuthUserService,
    public shareService : SharerService

  ) {
    this.id = this.route.snapshot.paramMap.get('id');
    this.gameModel = new Model('Game',requesService);

   }

  ngOnInit() {
    this.init();
    this.analyzeParameters();
  }

  async init(event = null){

    if(this.firstLoading){
      await this.dialogService.loadingProgres();
      this.firstLoading = false;
    }
    this.gameModel.api_functionModel(this.id,'dataProfile').subscribe(data => {
      if(data['status'] == 'success')
        this.game = data['data'];
      //console.log('Game',this.game);
      this.isFinish();
      if(event)
        event.target.complete();
      this.dialogService.dimissLoaging();
    },
    error =>{
      this.dialogService.dimissLoaging(); 
    })
  }

  async addResult(){
    const modal = await this.modalController.create({
      component: AddResultPage,
      componentProps : {
        game : this.game
      }
    })

    modal.onDidDismiss().then(data => {
      if(data.data['game']){
        this.game = this.game;
        this.listsHomeService.remove('games',this.game.id);
        this.router.navigateByUrl('results/profile/' + this.game.id)
      }
    })


    await modal.present();
  }


  isFinish(){
    let dateString = `${this.game.date} ${this.game.time}`;
    
    let dateEnable = moment(dateString).add(2,'hours');
    let now = new Date();

    return moment(now).isAfter(dateEnable) 
      
  }

  confirmDelete(){
    this.dialogService.presentAlertConfirm('Confirmar','Estas seguro de eliminar este game',e => {
      this.delete()
    });
   }

   delete(){
     this.gameModel.api_delete(this.game.id).subscribe(data => {
       console.log(data);
       if(data['status'] == 'success'){
         this.dialogService.showToast('Partido Eliminado',null,'success');
         //this.router.navigateByUrl('/home/games',{ replaceUrl: true });
         //window.location.assign('#/home/games')
         this.listsHomeService.remove('games',this.game.id);
         this.navCtrl.navigateRoot('/home/games');
         //this.gameService.listDelete(game.id);
         //this.utilArray.listDelete(this.games,game.id)
         
       }
     });
   }


   analyzeParameters(){
     let reload = this.route.snapshot.queryParamMap.get('reload');
     console.log('reload:',reload)
   }
  
   get isAdminTeam(){
    return this.authUser.isAdminGame(this.game) || this.authUser.isAdmin();
  }

 

}
